export default "/* \r\n    el orden definido para los estilos, \r\n    es igual a como esta estructurado html\r\n    si no encuentras un etiqueta quizas no hubo\r\n    la necesidad de de darle estilos o esta en el\r\n    archivo principal styles.scss \r\n*/\n/*\r\n    importacion de la hoja de estilos que contiene\r\n    varaibales para reutilizar en la aplicacion\r\n    (ruta del archivo aplicacion/src/app/shared)...\r\n*/\n/*\r\n    los estilos de los contenedores de esta seccion estan\r\n    en style.scss por ser codigo reutilizable\r\n    execto content_title h5 y div .fa-arrow-left\r\n*/\n.content_title h5 {\n  color: #0084ff;\n  letter-spacing: 1.5px; }\ndiv .fa-arrow-left {\n  color: #0084ff; }\n/*\r\n    de la linea 40 hasta el final estan los estilos\r\n    de todos los elementos que contiene el formulario en \r\n    la linea 46/48 definimos un query ajustando el ancho\r\n    del formulario para dispositivos moviles\r\n*/\n.content_form form {\n  width: 340px;\n  padding: 20px 30px;\n  margin: 0 auto; }\n@media (max-width: 500px) {\n    .content_form form {\n      width: 300px; } }\n.content_form form .content_inputs {\n    margin-bottom: 20px; }\n.content_form form .content_inputs i {\n      background-color: #0084ff;\n      color: #fff;\n      margin-top: 2px; }\n.content_form form button {\n    border: none;\n    background-color: #fff;\n    color: grey;\n    padding: 8px 16px;\n    margin: 0 !important;\n    border-radius: 5px;\n    transition: all .3s ease; }\n.content_form form button:hover {\n      background-color: #0084ff;\n      color: #fff; }\n.content_form form .conten_icons {\n    text-align: center;\n    margin: 26px; }\n.content_form form .conten_icons i {\n      color: #fff;\n      margin: 0 10px;\n      padding: 12px;\n      border-radius: 100px;\n      box-shadow: 0 3px 10px #444242;\n      cursor: pointer;\n      transition: all .2s ease-in-out; }\n.content_form form .conten_icons i:hover {\n        transform: scale(1.2); }\n.content_form form .conten_icons i.fa-github {\n        background-color: #36465d; }\n.content_form form .conten_icons i.fa-envelope {\n        background-color: #db4437; }\n.content_form form .conten_icons i.fa-linkedin {\n        background-color: #0077b5; }\n.content_form form .content_error {\n    text-align: center;\n    background-color: #eb5858; }\n\r\n/*# sourceMappingURL=data:application/json;base64, */"